.order {
  background-image: url(../../img/procents.svg);
  background-position: bottom center;
  background-size: cover;
  padding: 87px 0;
  background-color: var(--accent-color);
}
.order__container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}
.order__left {
  padding-left: 60px;
  grid-column: 5 span;
}
.order__title {
  margin: 0;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 36px;
  line-height: 130%;
  color: var(--light-color);
}
.order__descr {
  margin: 0;
  font-weight: 400;
  font-size: 20px;
  line-height: 130%;
  color: var(--light-color);
}
.order__right {
  padding-right: 60px;
  display: flex;
  justify-content: flex-end;
  grid-column: 7 span;
}
.order__form {
  max-width: 500px;
}